<style scoped>
    .pages {
        text-align: left;
        padding: 0 20px;

    }

    .form {
        width: 100%;
        margin-top: 30px;
    }

</style>

<template>
    <div class="pages">
        <H3>基础信息</H3>
        <div class="line"></div>
        <el-form class="form" ref="form" :model="form" label-width="120px">
            <el-form-item label="账号">
                <el-input style="width: 200px" :disabled="isNoEdit" v-model="form.mobile" placeholder="请输入"></el-input>
            </el-form-item>
            <el-form-item label="密码">
                <el-input style="width: 200px" v-model="form.password" placeholder="请输入"></el-input>
            </el-form-item>
            <el-form-item label="确认密码">
                <el-input style="width: 200px" v-model="form.confirm_pass" placeholder="请输入"></el-input>
            </el-form-item>

            <el-form-item style="margin-top: 100px">
                <el-button type="primary" style="margin-left: 50px" :loading="isLoading"
                           @click="onSubmit">提交
                </el-button>
                <el-button @click="back">返回</el-button>
            </el-form-item>
        </el-form>
    </div>
</template>

<script>

    import api from '../../js/api/member/user'
    import baseApi from '../../js/api/baseApi'
    export default {
        data() {
            return {
                imageUrl: '',
                form: {
                    id: null,
                },
                img: null,
                isNoEdit: true,
                isLoading: false,
                options: [],
            }
        },
        mounted() {
            this.init();
        },
        methods: {
            init() {
                if (this.$route.query.id !== undefined) {
                    this.isNoEdit = true;
                    this.loadData()
                } else {
                    this.isNoEdit = false;
                }
            },
            loadData() {
                api.detail({id: this.$route.query.id}, res => {
                    this.form = res.data.data;
                })
            },
            edit() {
                this.isNoEdit = false;
            },
            onSubmit() {
                if (!baseApi.isStrValid(this.form.mobile)) {
                    this.$message.error('请输入账号');
                    return;
                }

                if (!baseApi.isStrValid(this.form.password)) {
                    this.$message.error('请输入密码');
                    return;
                }

                if (!baseApi.isStrValid(this.form.confirm_pass)) {
                    this.$message.error('请再次输入密码');
                    return;
                }

                if (this.form.password != this.form.confirm_pass) {
                    this.$message.error('请输入相同的密码');
                    return;
                }
                let data = baseApi.objToFormData(this.form);
                this.isLoading = true;
                api.create(data, () => {
                    this.isLoading = false;
                    this.$message.success('保存成功!');
                    this.$router.push({name: 'user1'});
                    this.$route.meta.keepAlive = false;
                },()=>{
                    this.isLoading = false;
                })
            },
            back() {
                this.$router.back();
            },

        }
    }
</script>
