import Api from './../baseApi'

export default class userApi {
    static index(data, callback, errorBack) {
        console.log(Api.url());
        Api.request(Api.url() + 'user1/entry', callback, errorBack, data)
    }

    static create(data, callback, errorBack) {
        Api.request(this.uri() + 'create', callback, errorBack, data)
    }

    static delete(data, callback, errorBack) {
        Api.request(this.uri() + 'delete', callback, errorBack, data)
    }
    static detail(data, callback, errorBack) {
        Api.request(this.uri() + 'detail', callback, errorBack, data)
    }

    static uri() {
        return Api.url() + 'user/'
    }

}
